import { NextPage } from 'next'
import { Fragment } from 'react'
import { useRootData } from '../hooks/useRootData'

const Events: NextPage = () => {
    const { cities } = useRootData(store => ({
        cities: store.filteredCities
    }))

    return (
        <Fragment>
            <h1>Events</h1>
            <ul>
                {cities.map(city => (
                    <li key={city}>{city}</li>
                ))}
            </ul>
        </Fragment>
    )
}

export default Events
